'use client';
import { useState, useRef, useEffect, FormEvent } from 'react';
import { Bot, User, Send, Loader2 } from 'lucide-react';

interface Message {
  id: string;
  role: 'user' | 'ai' | 'error';
  text: string;
}

export const ChatAssistant = () => {
    const [messages, setMessages] = useState<Message[]>([]);
    const [input, setInput] = useState('');
    const [isLoading, setIsLoading] = useState(false);
    const messagesEndRef = useRef<HTMLDivElement>(null);

    const scrollToBottom = () => {
        messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
    };

    useEffect(scrollToBottom, [messages]);

    const handleSubmit = async (e: FormEvent) => {
        e.preventDefault();
        if (!input.trim() || isLoading) return;

        const userMessage: Message = { id: Date.now().toString(), role: 'user', text: input };
        setMessages(prev => [...prev, userMessage]);
        setIsLoading(true);
        setInput('');

        try {
            const res = await fetch(`${process.env.NEXT_PUBLIC_API_BASE_URL}/api/chat`, {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({ question: input }),
            });

            if (!res.ok) {
                const errData = await res.json();
                throw new Error(errData.detail || 'Failed to get a response from the assistant.');
            }

            const data = await res.json();
            const aiMessage: Message = { id: (Date.now() + 1).toString(), role: 'ai', text: data.answer };
            setMessages(prev => [...prev, aiMessage]);

        } catch (error) {
            const errorMessage: Message = { id: (Date.now() + 1).toString(), role: 'error', text: 'Sorry, something went wrong. Please try again.' };
            setMessages(prev => [...prev, errorMessage]);
        } finally {
            setIsLoading(false);
        }
    };

    return (
        <div className="flex flex-col h-[70vh] bg-gray-100 rounded-lg border">
            <div className="flex-1 overflow-y-auto p-6 space-y-6">
                {messages.map(msg => (
                    <div key={msg.id} className={`flex items-start gap-4 ${msg.role === 'user' ? 'justify-end' : ''}`}>
                        {msg.role !== 'user' && (
                            <div className={`p-2 rounded-full text-white ${msg.role === 'ai' ? 'bg-blue-600' : 'bg-red-600'}`}>
                                <Bot size={20} />
                            </div>
                        )}
                        <div className={`max-w-xl p-4 rounded-lg shadow-sm ${
                            msg.role === 'user' ? 'bg-blue-500 text-white' : 
                            msg.role === 'ai' ? 'bg-white border text-gray-800' :
                            'bg-red-100 text-red-800'
                        }`}>
                            <p className="text-sm leading-relaxed">{msg.text}</p>
                        </div>
                        {msg.role === 'user' && (
                           <div className="p-2 rounded-full bg-gray-300 text-gray-700">
                               <User size={20}/>
                           </div>
                        )}
                    </div>
                ))}
                 {isLoading && (
                    <div className="flex items-start gap-4">
                        <div className="p-2 rounded-full bg-blue-600 text-white animate-pulse">
                           <Bot size={20}/>
                        </div>
                       <div className="bg-white border p-3 rounded-lg flex items-center gap-2 text-gray-700">
                           <Loader2 className="animate-spin" size={20}/>
                           <span>Thinking...</span>
                       </div>
                    </div>
                 )}
                <div ref={messagesEndRef} />
            </div>
            <form onSubmit={handleSubmit} className="p-4 border-t bg-white rounded-b-lg flex items-center gap-3">
                <input
                    type="text"
                    value={input}
                    onChange={(e) => setInput(e.target.value)}
                    placeholder="Ask a question about any document..."
                    // --- THE FONT COLOR FIX IS HERE ---
                    className="flex-1 p-2 border rounded-lg focus:ring-2 focus:ring-blue-500 focus:outline-none text-gray-900"
                    disabled={isLoading}
                />
                <button type="submit" disabled={isLoading} className="p-2 bg-blue-600 text-white rounded-lg disabled:bg-gray-400 hover:bg-blue-700 flex items-center justify-center">
                    <Send size={20}/>
                </button>
            </form>
        </div>
    );
};